        font-family: $helvetica-medium;
        color: $primaryfontcolor;
        font-size: 20px;
        margin: 0px 0px 30px 0px;
    }
    .date-picker {
        position: relative;
        a {
            position: absolute;
            right: 10px;
            top: 5px;
        }
    }
    .cus-text-area {
        textarea {
            height: 100px;
        }
        label {
            position: relative;
            color: #9A9A9A;
            background: #fff;
            font-family: "webfontregular";
            font-size: 12px;
            text-transform: capitalize;
            padding: 0px 10px;
            top: 15px;
            left: 14px;
        }
        
    }

    .floating {
        margin-bottom: 2rem;
        position: relative;
        /*  background-color: var(--field__background);*/
        transition: background-color 0.2s ease;
        /*
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
    */
    }

    
    .floating i {
        position: absolute;
        top: 11px;
        right: 10px;
        color: #708090;
        font-size: 18px;
    }
    
    .floating__input {
        padding:5px 10px 5px 10px;
        font-size: 18px;
        border: 1px solid rgba(154, 154, 154, 0.5);
        transition: border-color 0.2s ease;
        border-radius: 6px;
        width: 100%;
        height: 40px;
        box-sizing: border-box;
        color: #708090;
        font-family: $helvetica-regular;
    }
    
    .floating__input:focus {
        outline: none;
    }
    

    
    .floating__input::placeholder {
        color: rgba(0, 0, 0, 0);
    }
    
    .floating__label {
        display: block;
        position: relative;
        max-height: 0;
        font-weight: 500;
        pointer-events: none;
    }
    
    .floating__label::before {
        font-family: $helvetica-regular;
        color: #708090;
        text-transform: capitalize;
        font-size: 14px;
        content: attr(data-content);
        display: inline-block;
        filter: blur(0);
        backface-visibility: hidden;
        transform-origin: left top;
        transition: transform 0.2s ease;
        left: 0.8rem;
        position: relative;
        top: 1px;
    }
    
    .floating__label::after {
        bottom: 1rem;
        content: "";
        height: 0.1rem;
        position: absolute;
        transition: transform 180ms cubic-bezier(0.4, 0, 0.2, 1),
            opacity 180ms cubic-bezier(0.4, 0, 0.2, 1), background-color 0.3s ease;
        opacity: 0;
        left: 0;
        top: 100%;
        margin-top: -0.1rem;
        transform: scale3d(0, 1, 1);
        width: 100%;
        /*  background-color: var(--color__accent);*/
    }
    
    .floating__input:focus + .floating__label::after {
        transform: scale3d(1, 1, 1);
        opacity: 1;
    }
    
    .floating__input:placeholder-shown + .floating__label::before {
        transform: translate3d(0, -2.2rem, 0) scale3d(1, 1, 1);
    }
    
    .floating__label::before,
    .floating__input:focus + .floating__label::before {
        transform: translate3d(0, -3.30rem, 0) scale3d(0.82, 0.82, 1);
    }
    
    .floating__input:focus + .floating__label::before {
        color: $thirdfontcolor;
        background: #fff;
        font-family: $helvetica-regular;
        font-size: 15px;
        text-transform: capitalize;
        padding: 0px 10px;
    
    }
    .select-dropdown .floating__label::before {
        color: #9A9A9A;
        background: #fff;
        font-family: "webfontregular";
        font-size: 15px;
        text-transform: capitalize;
        padding: 0px 10px;
    }
    
    
    *,
    *:before,
    *:after {
        box-sizing: inherit;
    }
    
    .hidden--visually {
        border: 0;
        clip: rect(1px 1px 1px 1px);
        clip: rect(1px, 1px, 1px, 1px);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
    }
    
    .button {
        background: var(--color__accent);
        border-width: 0;
        color: #fff;
        display: block;
        width: 100%;
        padding: 1rem 2rem;
        font-weight: 600;
        font-size: 1.25rem;
        text-transform: uppercase;
        letter-spacing: 1px;
    }
}
